@import '../variables';

.header {
  border-bottom: 1px solid $gray-line;
  padding: 40px 0;

  @media (max-width: 645px) {
    padding-bottom: 0;
  }





  .container {
    display: flex;
    align-items: center;
    justify-content: space-between;

    @media (max-width: 645px) {
      flex-direction: column;
      .header__cart {
        margin-top: 30px;
      }    
    }
    
  }

  &__logo {
    display: flex;
    width: 230px;
    transition: all 0.2s ease-in-out;
    &:hover {
      transform: scale(1.03);
    }

    img {
      margin-right: 15px;
      
    }

    h1 {
      color: #181818;
      font-size: 24px;
      letter-spacing: 1%;
      text-transform: uppercase;
      font-weight: 800;
    }

    p {
      color: #7b7b7b;
    }
    
    @media (max-width: 645px) {
      flex-direction: column;
      align-items: center;
      text-align: center;
     
      }
    
  }
}
